.layout{
  background-color: #2a3236;
  background-image: url('../public/images/about.jpg'); /* Replace with your image URL */
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    position: relative;
   min-height: 150vh;
   color: #d9f5ea;
   overflow: hidden;
}
.border-bottom{
  border-bottom:1px rgb(196, 232, 244) solid !important;
}
.navbar-brand{
  font-size: 30px;
}
.navbar{
  box-shadow: 0 10px 8px -6px rgb(100, 100, 103);
  --webkit-box-shadow: 0 10px 8px -6px rgb(92, 96, 104);
}
.navbar li{
  font-size: 18px;
}
.navbar li:hover{
  font-size: 19px;
  color:white !important;
  border-bottom: 1px #fbe69e solid ;
}

.footer {
    color: white;
    padding-top: 8px;
    background: #1e2c3a; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to right,
      #434343,
      #1f2a36
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #434343,  #1f2a36);
    position: fixed;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    
 
  }
  .fa-cart-shopping{
    color:white;
    font-size: 3vh;
  }
  
  .footer a {
    text-decoration: none;
    color: white;
    padding: 10px;
  }
  
  .footer a:hover {
    color: #fbe69e;
    border-bottom: 1px solid #ffefba;
  }
  .pnf {
    display: flex;
    min-height: 75vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .pnf-title {
    font-size: 100px;
    font-weight: 700;
    color:#f7baba;
  }
  
  .pnf-heading {
    font-weight: normal;
    color:#d7ba8c;
  }
  
  .pnf-btn {
    color: rgb(141, 227, 188);
    border: 1px solid rgb(233, 251, 226);
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
  }
  .pnf-btn:hover {
    background-color: rgb(200, 244, 218);
    color: rgb(20, 18, 18);
  }
  .contactus {
    margin: 0;
    padding: 0;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color:white;
    
    background-size: cover;
    background-position: center;
    position: relative;
    
  }
  .contactus img{
    border: 2px white solid ;
    border-radius: 30px;
  }
  .contactus div h2{
    color:#f7baba;
    text-decoration: underline;
  }
  /* ==================== */
  .product-link {
    text-decoration: none !important;
    color: black !important;
  }
  
  .cat-btn {
    padding: 40px 0px;
    font-size: 24px;
    text-transform: uppercase;
    background-color: #d9f5ea;
  }
  .cat-btn:hover {
    background-color: #4b7759;
    color: white;
  }
 
  
  @media (max-width: 480px) {
    .contactus {
      font-size: 12px; /* Adjust height for smaller screens */
    }
    .contactus h2{
      font-size: 18px;
    }
    .col-md-4{
      margin: 5px !important;
    }

  .card-item .cart-remove-btn{
    display: inline-block;
    margin: 0px !important;
  }
  .layout{
    
    background-image: url('../public/images/bg.jpg'); /* Replace with your image URL */
      background-size: cover;
      background-position: center;
      background-attachment: fixed;
      position: relative;
   
  }
  }

  @media (max-width: 768px) {
    .contactus {
      font-size: 13px; /* Adjust height for smaller screens */
    }
    .contactus h2{
      font-size: 20px;
    }
    .col-md-4{
      margin: 5px !important;
    }
    .layout{
    
      background-image: url('../public/images/bg.jpg'); /* Replace with your image URL */
        background-size: cover;
        background-position: center;
        background-attachment: fixed;
        position: relative;
     
    }
  }
  .card-item div{
    display: inline-block;
  }
  
  /* login  */
 .login{
  background-image: url('../public/images/login.jpg');
  background-size: cover;
  background-position: center;
  border: 2px grey solid  ;
  color:white
 }
