.filterprice{
    color:rgb(233, 248, 191) !important;
}
.filter{
    color:rgb(208, 244, 232)
}
.card {
    border: 2px rgb(56, 40, 63) solid;
    border-radius: 15px;
    transition: box-shadow 0.3s ease;
    background-color:rgb(87, 87, 87);
}
.card:hover{
    box-shadow: 0 0 20px 5px rgba(77, 41, 103, 0.5);
}

.card-body{
    background-color: rgb(79, 93, 106);
    color:azure;
    border-bottom: 1px rgb(52, 51, 51) solid !important;
    border-radius: 10px !important;
}
.card-title{
    color:bisque
}
.fixed-sidebar {
    position: fixed;
  top: 15vh;
  bottom: 0;
  left: 0;
  width: 25% !important;
  overflow: hidden;
  }
  .sidebars{
    margin-left: 25%;
    overflow: hidden;
    width: fit-content;

  }

  @media only screen and (max-width: 600px) {
    .sidebars{
        margin-left: 40%;
    }
    .fixed-sidebar{
        width: 30%;
    }
   }
  @media only screen and (max-width: 500px) {
   .fixed-sidebar{
      display: none;
   }
   .sidebars{
    margin-left: 0px;
}
  }
.load{
    margin-bottom: 20vh !important;
}
 
